const state = {
    // 分类数据的数据
    categoryList: []
};
const mutations = {
    saveCategoryList(state, object) {
        state.categoryList = object;
    }
};
const actions = {
    saveCategoryList(context, object) {
        context.commit("saveCategoryList", object);
    }
};
export default { namespaced: true, state, mutations, actions }


