import Vue from 'vue'
import Router from 'vue-router'
import IndexRouter from '@/pages/index/conf/router'// 主模板
import HomeRouter from '@/pages/home/conf/router'// 首页模板
import CategoryRouter from '@/pages/category/conf/router'// 分类模板
import SearchRouter from '@/pages/search/conf/router'// 搜索模板
import CommoditydecRouter from '@/pages/commoditydec/conf/router'// 商品详情模板
import CommodityListRouter from '@/pages/commodityList/conf/router'// 商品列表模板

// 添加router路由插件
Vue.use(Router)

export default new Router({
  mode: 'hash', // hash/history访问路径不带井号
  routes: [].concat(IndexRouter, HomeRouter, CategoryRouter, SearchRouter, CommoditydecRouter,
    CommodityListRouter)
});



