import Vue from "vue";
import Vuex from "vuex";
import categoryStore from "../pages/category/conf/store";
import commodityListStore from "../pages/commodityList/conf/store";
Vue.use(Vuex);
const store = new Vuex.Store({
  modules: { commodityListStore, categoryStore },
});
export { store }


