const state = {
    // 路由传来的数据
    routeFromParams: {}
};
const mutations = {
    saveFromParams(state, object) {
        state.routeFromParams = object;
    }
};
const actions = {
    saveFromParams(context, object) {
        context.commit("saveFromParams", object);
    }
};
export default { namespaced: true, state, mutations, actions }


